import { memo } from 'react'
import css from './Form.module.scss'
import classnames from 'classnames'
import { PropsWithClassName } from 'specs/global.spec'
import useManageForm, { FieldsData, Names } from 'hooks/form/useManageForm'
import { Controller } from 'react-hook-form'
import Input from 'components/atoms/Input/Input'
import Button from 'components/atoms/Button/Button'
import { ButtonHeights, ButtonVariants } from 'components/atoms/Button/Button.spec'
import { ButtonTypes } from 'components/atoms/Button/ButtonCore.spec'
import SelectCustomized from 'components/atoms/SelectCustomized/SelectCustomized'
import { SelectCustomOptionType } from 'components/atoms/SelectCustomized/SelectCustomized.spec'
import Datepicker from 'components/atoms/Datepicker/Datepicker'
import { InputVariants } from 'components/atoms/Input/Input.spec'
import CheckboxBattery from 'components/molecules/CheckboxBattery/CheckboxBattery'
import { CheckboxBatteryItemType } from 'components/molecules/CheckboxBattery/CheckboxBattery.spec'
import { MapMarkerRequestValueType } from 'components/molecules/MapMarkerRequest/MapMarkerRequest.spec'
import useModal from 'hooks/ui/useModal'
import IconMap from 'assets/icons/IconMap'

function Form ({
  className
}: PropsWithClassName) {
  const {
    control,
    handleSubmitData,
    colorOptions,
    colorErrorMessage
  } = useManageForm()

  const {showMapModal} = useModal()

  const {
    [Names.DATE]: {
      errorMessage: dateRequiredError,
      name: dateName,
      label: dateLabel,
    },
    [Names.OCCUPATION]: {
      errorMessage: occupationRequiredError,
      name: occupationName,
      label: occupationLabel,
    },
    [Names.WINTER]: {
      errorMessage: winterRequiredError,
      name: winterName,
      label: winterLabel,
    },
    [Names.GENDER]: {
      errorMessage: genderRequiredError,
      name: genderName,
      label: genderLabel,
      options: genderOptions
    },
    [Names.REASON]: {
      errorMessage: reasonRequiredError,
      name: reasonName,
      label: reasonLabel,
    },
    [Names.COLORS]: {
      name: colorsName,
      label: colorsLabel,
    },
    [Names.MAP]: {
      name: mapName,
      label: mapLabel,
      errorMessage: mapRequiredError
    }
  } = FieldsData

  return (
    <div className={classnames(css.wrapper, className)}>
      <form onSubmit={handleSubmitData}>
        <fieldset className={css.grid}>
          <Controller
            render={({field: {value, onChange, name}, fieldState: {error}}) => (
              <Input
                value={value as string}
                onChange={onChange}
                name={name}
                label={occupationLabel}
                error={error?.message}
              />
            )}
            name={occupationName}
            control={control}
            rules={{
              required: occupationRequiredError
            }}
          />
          <Controller
            render={({field: {value, onChange, name}, fieldState: {error}}) => (
              <Input
                value={value as string}
                onChange={onChange}
                name={name}
                label={winterLabel}
                error={error?.message}
              />
            )}
            name={winterName}
            control={control}
            rules={{
              required: winterRequiredError
            }}
          />
          <Controller
            render={({field: {value, onChange, name}, fieldState: {error}}) => (
              <SelectCustomized
                value={value as unknown as SelectCustomOptionType}
                onChange={onChange}
                label={genderLabel}
                placeholder={``}
                options={genderOptions as SelectCustomOptionType[]}
                name={name}
                error={error?.message}
                isSearchable={false}
                hideSelectedOptions
              />
            )}
            name={genderName}
            control={control}
            rules={{
              required: genderRequiredError
            }}
          />
          <Controller
            render={({field: {value, onChange}, fieldState: {error}}) => (
              <Datepicker
                value={value}
                onChange={onChange}
                label={dateLabel}
                isHoursPickRequired={false}
                error={error?.message}
              />
            )}
            name={dateName}
            control={control}
            rules={{
              required: dateRequiredError
            }}
          />
          <Controller
            render={({field: {value, onChange, name}, fieldState: {error}}) => (
              <Input
                value={value as string}
                onChange={onChange}
                name={name}
                label={reasonLabel}
                variant={InputVariants.TEXTAREA}
                error={error?.message}
              />
            )}
            name={reasonName}
            control={control}
            rules={{
              required: reasonRequiredError
            }}
          />
          <CheckboxBattery
            label={colorsLabel}
            control={control}
            namespace={colorsName}
            options={colorOptions as CheckboxBatteryItemType[]}
            groupErrorMessage={colorErrorMessage}
          />
          <Controller
            render={({field: {value, onChange, name}, fieldState: {error}}) => (
              <Input
                value={value ? JSON.stringify(value) : ``}
                onChange={onChange}
                onClick={() => showMapModal(value as MapMarkerRequestValueType, onChange)}
                name={name}
                label={mapLabel}
                placeholder={`    `}
                className={css.inputMap}
                error={error?.message}
              >
                <IconMap
                  className={classnames(css.iconMap, {
                    [css.iconMapError]: error?.message
                  })}
                />
              </Input>
            )}
            name={mapName}
            control={control}
            rules={{
              required: mapRequiredError
            }}
          />
        </fieldset>
        <Button
          type={ButtonTypes.SUBMIT}
          className={css.button}
          variant={ButtonVariants.FILLED}
          height={ButtonHeights.REGULAR}
        >
           
        </Button>
      </form>
    </div>
  )
}

export default memo(Form)
