import { useCallback, useEffect, useMemo } from 'react'
import { useFieldArray, useForm } from 'react-hook-form'
import { SelectCustomOptionType } from 'components/atoms/SelectCustomized/SelectCustomized.spec'
import { CheckboxBatteryItemType } from 'components/molecules/CheckboxBattery/CheckboxBattery.spec'

export const Names = {
  DATE: `DATE`,
  OCCUPATION: `OCCUPATION`,
  WINTER: `WINTER`,
  GENDER: `GENDER`,
  REASON: `REASON`,
  COLORS: `COLORS`,
  MAP: `MAP`
}

export const FieldsData = {
  [Names.DATE]: {
    name: Names.DATE,
    label: ` `,
    errorMessage: `,  `
  },
  [Names.OCCUPATION]: {
    name: Names.OCCUPATION,
    label: `  `,
    errorMessage: `,    `
  },
  [Names.OCCUPATION]: {
    name: Names.OCCUPATION,
    label: `  `,
    errorMessage: `,    `
  },
  [Names.WINTER]: {
    name: Names.WINTER,
    label: `      ?`,
    errorMessage: `,     `
  },
  [Names.GENDER]: {
    name: Names.GENDER,
    label: ` `,
    errorMessage: `,   `,
    options: [
      {
        label: ``,
        value: `male`
      },
      {
        label: ``,
        value: `female`
      },
    ] as SelectCustomOptionType[]
  },
  [Names.REASON]: {
    name: Names.REASON,
    label: `    ?`,
    errorMessage: `,      `
  },
  [Names.COLORS]: {
    name: Names.COLORS,
    label: `         ?    `,
    errorMessage: `,     `,
    options: [
      ``,
      ``,
      ` `,
      ``,
      ``,
      ``,
      ``,
    ],
    labelOther: ` `
  },
  [Names.MAP]: {
    name: Names.MAP,
    label: `, ,   `,
    errorMessage: `,  `
  }
}

function useManageForm () {
  const colorsDefaultValues: CheckboxBatteryItemType[] = useMemo(() => {
    const baseResults = (FieldsData[Names.COLORS].options as string[]).map((item, index) => ({
      id: index,
      label: item,
      value: false
    }))

    return [
      ...baseResults,
      {
        id: baseResults.length,
        label: ``,
        value: false,
        isCustom: true
      }
    ]
  }, [])

  const {control, handleSubmit, formState} = useForm({
    mode: `onChange`,
    defaultValues: {
      [Names.DATE]: Date.now(),
      [Names.OCCUPATION]: ``,
      [Names.WINTER]: ``,
      [Names.GENDER]: null,
      [Names.REASON]: ``,
      [Names.COLORS]: colorsDefaultValues,
      [Names.MAP]: null
    },
  })

  const {fields} = useFieldArray({
    name: Names.COLORS,
    control,
    rules: {
      validate: value => {
        console.log(value)
        const isAnyChecked = (value as CheckboxBatteryItemType[]).reduce((total, item) => {
          if (!total && item.value) {
            total = true
          }

          return total
        }, false)

        return isAnyChecked || FieldsData[Names.COLORS].errorMessage
      }
    }
  })

  const handleSubmitData = useCallback(data => {
    alert(`  :` + JSON.stringify(data))
  }, [])

  return {
    control,
    handleSubmitData: handleSubmit(handleSubmitData),
    colorOptions: fields,
    colorErrorMessage: formState.errors[Names.COLORS]?.root?.message
  }
}

export default useManageForm
